import React, { Component } from 'react';
import axios from 'axios';
import Movies from './Movies';
import Loading from './Loading';
import './Section.scss';


class Section extends Component {
    state = {
        kim : [],
        isLoading: true,
    }
    // state를 선언 후 밑에서 setState로 state를 어떻게 바꿀것인지 말해줌
    getMovies =  async () => {
        const {data: {data: {movies}}}= await axios.get("https://yts.mx/api/v2/list_movies.json")
        // movies는 객체안에있는 key이름
        this.setState({
            kim : movies
            // 우리가 정한 state 이름 : key 이름
        })
    }
    componentDidMount() {
        // render() 수행 후 1번 호출되는 함수
        setTimeout(() => {     
            this.setState({
                isLoading: false,
            })
        }, 5000);
        this.getMovies();
    }

    render() {
        const {kim, isLoading} = this.state;  //kim이 []를 가르키고, []는 movies를 가르킴
        return (
            <>
                {
                    isLoading ? <Loading/> :   <section className="container">
                    {
                        kim.map((data)=>(
                        <Movies key={data.id} year={data.year} title={data.title} summary={data.summary} poster={data.medium_cover_image} genres={data.genres} />
                        ))
                    }
                </section> 
                }
             
            </>
         
        );
    }
}

export default Section;